<script>
import ServiceSection from '../../components/ServiceSection.vue'
import FeaturedServicesSection from '../../components/FeaturedServicesSection.vue'
import RecommendationsSection from '../../components/RecommendationsSection.vue'
import VIPSection from '../../components/VIPSection.vue'
import ServiceGallerySection from '../../components/ServiceGallerySection.vue'

export default {
	components: {
		ServiceSection,
		FeaturedServicesSection,
		RecommendationsSection,
		VIPSection,
		ServiceGallerySection
	},
	data() {
		return {
			categoryTabs: [
				{ label: "设计师", active: false },
				{ label: "优服务", active: true },
				{ label: "品牌馆", active: false },
			],
		}
	},
	methods: {
		goBack() {
			uni.navigateBack()
		},
		switchTab(index) {
			this.categoryTabs.forEach((tab, i) => {
				tab.active = i === index
			})
			
			// 跳转到对应页面
			if (index === 0) {
				uni.redirectTo({
					url: '/pages/designer/index'
				})
			} else if (index === 2) {
				uni.redirectTo({
					url: '/pages/brand/index'
				})
			}
		}
	}
}
</script>

